import React, {useState, useCallback, useEffect} from 'react';


function Parent() {
    const [count, setCount] = useState(1);
    const [val, setVal] = useState('');
 
    const callback = useCallback(() => {
        return count;
    }, [count]);
    const aa = () => {
        console.log(222);
    }
    return <div>
        <h4>{count}</h4>
        <Child callback={callback} aa={aa}/>
        <div>
            <button onClick={() => setCount(count + 1)}>+</button>
            <input value={val} onChange={event => setVal(event.target.value)}/>
        </div>
    </div>;
}
 
function Child({ callback, aa }) {
    const [count, setCount] = useState(() => callback());
    const [count1, setCount1] = useState(() => aa());
    useEffect(() => {
        console.log(111);
        setCount(callback());
    }, [callback]);
    useEffect(() => {
        console.log(233);
        setCount1(aa());
    }, [aa]);
    return <div>
        {count}
        {count1}
    </div>
}

const UseCallback = () => {

    return (
        <div>
            <Parent />
        </div>
    )
}

export default UseCallback