import React, {useState, useCallback, useEffect, useRef} from 'react';
import Anyting from '../components/Anything';

const UseRef = () => {
    const refDemo = useRef(0)
    const [num, setNum] = useState(0)
    const [status, setStatus] = useState(false);
    const [filterData, setFilterData] = useState({currentClassId: 1, currentClassName: 'a'});
    const addEvent = () => {
        setNum(data => {
            console.log(data);
            return ++data
        });
        console.log(num);
        refDemo.current = refDemo.current + 1;

    }
    const selectClass = (currentClass) => {
        let noClassId = -2;
        let newFilterData = [...filterData];
        if (currentClass.classId !== noClassId) {
            newFilterData.classId = currentClass.classId;
            delete newFilterData.className;
        } else {
            delete newFilterData.classId;
            newFilterData.className = currentClass.className;
        }
        setFilterData(newFilterData);
    };
    const AAA = () => {
        return (
            <Anyting 
            status={status} 
            setStatus={setStatus} 
            currentClassId={filterData.currentClassId} 
            currentClassName={filterData.currentClassName}
            fn={selectClass}
            ></Anyting>
        )
    }
    return (
        <div>
            <button onClick={addEvent}>加1</button>
            <div>ref: {refDemo.current}</div>
            <div>useState: {num}</div>
            <AAA/>
        </div>
    )
}

export default UseRef