import React, { useEffect, useState } from 'react';
import { DatePicker, Switch, Input, Radio, Popover, message } from 'antd';
import locale from 'antd/es/date-picker/locale/zh_CN';
import moment from 'moment-timezone';
import '../../css/layout.css'
moment.tz.setDefault("Asia/Shanghai");
const format = 'YYYY-MM-DD HH:mm';
const HOMEWORK_TYPE = 8;
const historyhomeworkData = {
  correctWay: 1,
  courseSingleId: 278637,
  currentTime: 1616739380245,
  duration: null,
  endTime: 1616591400000,
  isUpLoadAnswerProcess: false,
  redoNum: 0,
  startTime: 1616575200000
}
const homeworkType = 8;
const usePhase = 2;
const UseDatepicker = () => {
  const [redoInfo, setRedoInfo] = useState({
    closeTime: null,
    correctWay: 1,
    duration: null,
    homeworkId: 73416043,
    isUpLoadAnswerProcess: false,
    startTime: null,
    usePhase: 2,
  })
  const disabledKQDateTime = (current) => {
    let currentTime = historyhomeworkData.startTime;
    const baseHour = 24;
    const baseMinute = 60;
    // 不可选择时间
    let startTimeDate = moment(currentTime).get('date');
    let closeTimeDate = moment(current).get('date');
    //不可选时间
    const disabledMinutes = moment(current).hour() < moment(currentTime).hour() ? 0 : moment(currentTime).minute();
    const disabledHour = moment(currentTime).minute() ? moment(currentTime).hour() + 1 : moment(currentTime).hour();
    let hour = startTimeDate === closeTimeDate ? disabledHour : 0;
    let minute = startTimeDate === closeTimeDate ? disabledMinutes : 0;
    return {
      disabledHours: difference(hour, baseHour),
      disabledMinutes: difference(minute, baseMinute)
    };
  };

  const difference = (end, length) => {
    if (!end) {
      return [];
    }
    const arr = Array.from(new Array(end).keys());
    const timeArray = Array.from(new Array(length).keys());
    return timeArray.filter(v => {
      return !arr.includes(v);
    });
  };
  const disabledDate = (type, current) => {
    console.log(type, current);
    // true 为不可选
    if (!current) {
      return false;
    }

    let currentTime = type === 'start' ? historyhomeworkData.currentTime : redoInfo.startTime || historyhomeworkData.currentTime;
    // console.log(moment(current).format("YYYY-MM-DD"), currentTime);
    //不可选日期
    if (homeworkType === HOMEWORK_TYPE && usePhase && historyhomeworkData.startTime && historyhomeworkData.currentTime < historyhomeworkData.startTime) {
      return moment(current).format("YYYY-MM-DD") > moment(historyhomeworkData.startTime).format("YYYY-MM-DD") || moment(current).format("YYYY-MM-DD") < moment(currentTime).format("YYYY-MM-DD");
    }
    return moment(current).endOf('day') < moment(currentTime).endOf('day');
  };
  const disabledTime = (type, current) => {
    if (!current) {
      return {
        disabledHours: () => [],
        disabledMinutes: () => []
      };
    }
    let disabledHour = [];
    let disabledMinute = [];

    if (homeworkType === HOMEWORK_TYPE && usePhase && historyhomeworkData.startTime && historyhomeworkData.currentTime < historyhomeworkData.startTime) {
      disabledHour = disabledKQDateTime(current).disabledHours;
      disabledMinute = disabledKQDateTime(current).disabledMinutes;
    }
    let currentTime = type === 'start' ? historyhomeworkData.currentTime : redoInfo.startTime || historyhomeworkData.currentTime;
    // 不可选择时间
    let startTimeDate = moment(currentTime).format("YYYY-MM-DD");
    let closeTimeDate = moment(current).format("YYYY-MM-DD");
    //不可选时间
    const disabledMinutes = moment(current).hour() > moment(currentTime).hour() ? 0 : moment(currentTime).minute();
    let hour = startTimeDate === closeTimeDate ? moment(currentTime).hour() : 0;
    let minute = startTimeDate === closeTimeDate ? disabledMinutes : 0;

    disabledHour = Array.from(new Array(hour).keys()).concat(disabledHour);
    disabledMinute = Array.from(new Array(minute).keys()).concat(disabledMinute);

    return {
      disabledHours: () => disabledHour,
      disabledMinutes: () => disabledMinute
    };
  };
  const changeTodayTime = async (date, dataType) => {
    const minSec = 3;  //最小差值
    if (date) {
      if (Math.abs(date.valueOf() - moment().valueOf()) <= minSec) {

        const currentTime = 1616737337957;
        setRedoInfo((draft) => {
          if (dataType === 'start') {
            draft.startTime = currentTime;
          } else {
            draft.closeTime = currentTime;
          }
        });
      } else {
        if (date) {
          setRedoInfo((draft) => {
            if (dataType === 'start') {
              draft.startTime = moment(date.format('YYYY-MM-DD HH:mm:00')).valueOf();
            } else {
              draft.closeTime = moment(date.format('YYYY-MM-DD HH:mm:00')).valueOf();
            }
            return draft;
          });
        }
      }
    }
  };
  const dateRender = (current) => {
    let style = {};
    const date = current.format().split('T')[0];
    const dateNow = moment(historyhomeworkData.currentTime).format('YYYY-MM-DD');
    if (date === dateNow) {
      style.border = '1px solid #1FB5AB';
      style.fontWeight = 'bold';
      style.color = '#1FB5AB';
    }
    return (
      <div className={`ant-calendar-date`} style={style}>
        {current.date()}
      </div>
    );
  };
  const aa = [
    {
      value: 1618480090958,
    },
    {
      value: 1618380090958,
    }]
  console.log(moment(new Date(), 'YYYY-MM-DD HH:MM:SS'));
  return (
    <div>
      <DatePicker
        locale={locale}
        format={format}
        // value={redoInfo.startTime ? moment(redoInfo.startTime) : undefined} //moment(1616737337957)
        // allowClear={false}
        disabledDate={e => disabledDate('start', e)}
        // defaultPickerValue={moment(1616756463890)}
        // disabledTime={e => disabledTime('start', e)}
        placeholder={'请选择时间'}
      // onChange={date => changeTodayTime(date, 'start')}
      // dateRender={dateRender}
      // showToday={{ format: "HH:mm" }}
      />
      <i class="triangle triangle-up"></i>
      <i class="triangle triangle-right"></i>
      <i class="triangle triangle-down"></i>
      <i class="triangle triangle-left"></i>
      {aa.map((item, index) => {
        console.log(item.value);
        return <div key={1}>
          <DatePicker value={moment(item.value)} format={'YYYY/MM/DD HH:MM'} disabled />
        </div>
      })}
      <DatePicker value={moment(1618480090958)} format={'YYYY/MM/DD HH:MM'} disabled />
    </div>
  )
}

export default UseDatepicker
