import React, { Component } from "react";
import { Carousel, Tooltip } from "antd";
import "../../css/layout.css";
const contentStyle = {
  height: "160px",
  color: "#fff",
  lineHeight: "160px",
  textAlign: "center",
  background: "#364d79",
};
const settings = {
  autoplay: true,
  autoplaySpeed: 2000,
  draggable: true,
  arrows: true,
};

class SwiperNav extends Component {
  // constructor(){
  //     super()
  // }

  render() {
    return (
      <div className="swiperBox">
        <Carousel {...settings}>
          <div>
            <h3 style={contentStyle}>1</h3>
          </div>
          <div>
            <h3 style={contentStyle}>2</h3>
          </div>
          <div>
            <h3 style={contentStyle}>3</h3>
          </div>
          <div>
            <h3 style={contentStyle}>4</h3>
          </div>
        </Carousel>
        {/* arrowPointAtCenter */}
        <div style={{content: '', height: '40px', display:'inline-block'}}></div>
        <Tooltip 
        arrowPointAtCenter
        title={<div className="title">112</div>} 
        overlayClassName="overLay"
        placement="rightTop" >
          <span className="children">112</span>
        </Tooltip>
      </div>
    );
  }
}

export default SwiperNav;
