import React, { Component } from 'react';
import * as THREE from 'THREE';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import img from '../../image/Earth.png'
import "../../css/trigger.css";
 
class ThreeBim extends Component {
    constructor(props) {
        super(props); 
        this.state = {  };
    }
 
    initThree(){
        threeStart();
 
        var renderer;
 
        function init() {
            renderer = new THREE.WebGLRenderer();
            // renderer.antialias = true
            renderer.setSize(1400 , 800);//设置渲染区域尺寸
            renderer.setClearColor(0xb9d3ff, 1); //设置背景颜色
            document.getElementById('canvas-frame').appendChild(renderer.domElement);
            console.log(renderer);
        }
 
        var camera;
        function initCamera() {
            // camera = new THREE.PerspectiveCamera(45, width / height, 1, 10000);
            // camera.position.set(400,400,400)
            // camera.up.set(0,1,0);
            // camera.lookAt(0,0,0);
            var width = window.innerWidth; //窗口宽度
            var height = window.innerHeight; //窗口高度

            var k = width / height; //窗口宽高比
            var s = 400; //三维场景显示范围控制系数，系数越大，显示的范围越大
            //创建相机对象
            camera = new THREE.OrthographicCamera(-s * k, s * k, s, -s, 1, 1000);
            camera.position.set(400, 0, 300); //设置相机位置
            camera.lookAt(0,0,0); //设置相机方向(指向的场景对象)
        }
 
        var scene;
        function initScene() {
            scene = new THREE.Scene();
        }
 
        var light;
        function initLight() {
            light = new THREE.AmbientLight(0xFFFFFF);
            light.position.set(300, 300, 0);
            scene.add(light);
        }
 
        function initObject() {
            // var geometry = new THREE.BoxGeometry(200, 200, 200);
            var geometry = new THREE.SphereGeometry(200, 40, 40); //创建一个球体几何对象
            // var material = new THREE.MeshLambertMaterial({ color: 0xFF0000 });
            // var mesh = new THREE.Mesh(geometry, material);
            // mesh.position.set(0, 0, 0);
            // scene.add(mesh);
            
            var textureLoader = new THREE.TextureLoader();
            textureLoader.load(img, function(texture) {
                var material = new THREE.MeshLambertMaterial({
                    // color: 0x0000ff,
                    // 设置颜色纹理贴图：Texture对象作为材质map属性的属性值
                    map: texture,//设置颜色贴图属性值
                }); //材质对象Material
                var mesh = new THREE.Mesh(geometry, material); //网格模型对象Mesh
                scene.add(mesh); //网格模型添加到场景中

                //纹理贴图加载成功后，调用渲染函数执行渲染操作
                // render();
            })
        }        
 
        // function render() {
        //     renderer.render(scene,camera);//执行渲染操作
        //     // mesh.rotateY(0.01);//每次绕y轴旋转0.01弧度
        //     requestAnimationFrame(render);//请求再次执行渲染函数render
        // }
        function threeStart() {
            init();
            initScene();
            initCamera();
            initLight();
            initObject();
            animation();
            // render();
            console.log(scene);
        }
        function animation() {         
            renderer.render(scene, camera);
            const orbitControls = new OrbitControls(camera, renderer.domElement);//创建控件对象
            orbitControls.minPolarAngle = Math.PI / 2
            orbitControls.maxPolarAngle = Math.PI / 2
            requestAnimationFrame(animation);
        }
    }
 
    /**
     * 开始Three
     *
     * @memberof ThreeBim
     */
    componentDidMount(){
        this.initThree();
    }
    render() {
        return (
            <div id='canvas-frame'></div>
        );
    }
}
 
export default ThreeBim;