import React from 'react';
import './index.css';

const Anyting = (props) => {
    const {status, setStatus, currentClassId, currentClassName, fn} = props
    const classes = []
    const handleDate = () => {
        console.log(status, 'status');
        setStatus(!status);
    };
    const setClassName = () => {
        if (currentClassId) {
            let currentClass = classes.filter(item => item.classId === currentClassId);
            if (currentClass.length) {
                return currentClass[0].className;
            }
        }
        return currentClassName;
    };
    const classChange = (e) => {
        // fn({
        //     classId: noClassId,
        //     className: e.target.value
        // });
        // setIsOption(false);
        // setOption(false);
    };
    const handleClick = (e) => {
        // e.nativeEvent.stopImmediatePropagation();
        // console.log(isOption);
        
        // setIsOption(!isOption);
        // setOption(!Option);
        // console.log(Option, 'a');
        
    };
    const clickClasses = (currentClass) => {
        fn(currentClass);
        // setIsOption(!isOption);
        // setOption(!Option);
    };
    return (
    <div className={"container"}>
        <input value={setClassName()} onChange={classChange} className={"input"} placeholder="请选择班级"/>
        <div className={"selectIco"} onClick={(e) => handleClick(e)}>{">"}</div>
        {
            status ? 
                <div className={"content"}>
                    {
                        classes.length ? 
                            classes.map(item => <div 
                                onClick={() => clickClasses(item)} 
                                // className={`${"option"} ${item.classId === selectClassId ? "checked" : ''}`} 
                                key={item.classId}>
                                {item.className}
                            </div>)
                            :
                            <span>没有数据</span>
                    }
                </div>
                :
                null
        }
    </div>
    )
}

export default Anyting