import React from "react";
// import "./App.css";
import "../css/layout.css";
import "../css/trigger.css";
import { Layout, Menu } from "antd";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import route from "../route/index";

const { Header, Sider, Content } = Layout;
const { SubMenu } = Menu;

class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: false,
    };
  }

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };
  makeMenuList(array) {
    console.log(array);
    let useData = route;
    let hasArg = false;
    if(array){
      hasArg = true;
      useData = array;
    }
    return useData.map((item, index) => {
      const pathMes = item.path.split('/')
      let title = hasArg ? pathMes[2] : pathMes[1]
      if (item.path !== '/') {
        // console.log(pathMes[1], item, len);
        return !item.children ? (
          <Menu.Item key={index} >
            <span> {title} </span>
            <Link to={item.path}> </Link>
          </Menu.Item>
        ) : (
          <Menu.SubMenu
            key={index}
            title={title}
            inlineCollapsed="true"
          >
            {this.makeMenuList(item.children)}
          </Menu.SubMenu>
        );
      }
    });
  }
  render() {
    return (
      <Layout
        style={{
          height: "100vh",
          width: "100vw",
        }}
      >
        <Router>
          <Sider
            trigger={""}
            width="300"
            defaultCollapsed={true}
            collapsible={true}
          >
            <div className="logo" />
            <Menu
              theme="dark"
              mode="inline"
              defaultSelectedKeys={window.location.pathname.slice(1)}
            >
              {this.makeMenuList()}
            </Menu>
          </Sider>
          <Layout className="site-layout">
            <Content
              className="site-layout-background"
              style={{
                margin: "24px 16px",
                padding: 24,
                minHeight: 280,
                height: "100vh",
              }}
            >
              {route.map((item, index) => {
                return (
                  <Route
                    path={item.path}
                    key={index}
                    component={item.component}
                  />
                );
              })}
            </Content>
          </Layout>
        </Router>
      </Layout>
    );
  }
}

export default Main;
